<template>
  <div id="dashboard">
      <ul class="collection with-header">
        <li class="collection-header"><h4>Employees</h4></li>
        <li v-for="employee in employees" :key="employee.id" class="collection-item">
          <div class="chip">{{employee.dept}}</div> {{employee.employee_id}}: {{employee.name}}
          <router-link :to="{ name: 'view-employee', params: {employee_id: employee.employee_id}}" class="secondary-content"><i class="fa fa-eye"></i></router-link>
        </li>
      </ul>
      <div class="fixed-action-btn">
        <router-link to="/new" class="btn-floating btn-large red">
          <i class="fa fa-plus"></i>
        </router-link>
      </div>

  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data: ()=>({
    employees:[]
  }),
  created() {
    //do something after creating vue instance
    db.collection('employees').orderBy('dept').get().then(querySnapshot=>{
      querySnapshot.forEach(resp => {
        const data = {
          'id': resp.id,
          'employee_id': resp.data().employee_id,
          'name': resp.data().name,
          'dept': resp.data().dept,
          'position': resp.data().position
        }
        this.employees.push(data)
      })
    })
  }
}
</script>



