<template>
  <div id="edit-employee">
      <h3>Edit Employee</h3>
      <div class="row">
        <form @submit.prevent="updateEmployee" class="col s12">
          <div class="row">
            <div class="input-field col s12">
              <input type="text" v-model="employee_id" class="validate"  disabled>
              <label class="active"> Employee ID#</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <input type="text" v-model="name" class="validate" required>
              <label class="active"> Employee Name</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <input type="text" v-model="dept" class="validate"  required>
              <label class="active"> Department</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <input type="text" v-model="position" class="validate"  required>
              <label class="active"> Position</label>
            </div>
          </div>
          <button type="submit" class="btn">Update</button>
          <router-link to="/" class="btn grey" >Cancel</router-link>
        </form>
      </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'edit-employee',
  data: ()=>({
     employee_id: null,
    name: null,
    dept: null,
    position:  null
  }),
  beforeRouteEnter(to, from, next){
      db.collection('employees').where('employee_id', '==', to.params.employee_id).get()
      .then(querySnapshot => {
      querySnapshot.forEach(resp =>{
        next(vm => {
          vm.employee_id = resp.data().employee_id
          vm.name = resp.data().name
          vm.dept = resp.data().dept
          vm.position = resp.data().position
          })
        })
      })
    },
    watch:{
      '$route': 'fetchData'
    },
    methods:{
      fetchData(){
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
      .then(querySnapshot=>{
      querySnapshot.forEach(resp =>{
          this.employee_id = resp.data().employee_id
          this.name = resp.data().name
          this.dept = resp.data().dept
          this.position = resp.data().position
      })
      })
      },
      updateEmployee(){
        if(confirm('Are you sure?')){
           db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
      .then(querySnapshot=>{
      querySnapshot.forEach(resp =>{
          resp.ref.update({
            employee_id: this.employee_id,
            name: this.name,
            dept : this.dept,
            position : this.position
             }).then(()=>{
               this.$router.push({name:"view-employee", params:{employee_id: this.employee_id}})
             })
            })
          })
        }
      }
    }
}
</script>




